"use client";

import { GlassCard } from "@/components/ui/glass-card";
import { Star } from "lucide-react";

const testimonials = [
  {
    name: "Sarah Johnson",
    role: "Professional Photographer",
    content:
      "ColorGradeX has revolutionized my workflow. The color transfer is so natural and the interface is incredibly intuitive.",
    rating: 5,
  },
  {
    name: "Michael Chen",
    role: "Digital Artist",
    content:
      "I love how I can maintain the original details while getting the exact color mood I want. It's like magic!",
    rating: 5,
  },
  {
    name: "Emma Davis",
    role: "Content Creator",
    content:
      "The best part is that it works entirely in my browser. No need to upload my images to any cloud service.",
    rating: 5,
  },
];

export function TestimonialSection() {
  return (
    <section className="py-20 px-4">
      <div className="max-w-6xl mx-auto">
        <h2 className="text-3xl md:text-4xl font-bold text-center mb-16 animate-fade-in">
          <span className="gradient-text">What Our Users Say</span>
        </h2>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {testimonials.map((testimonial, index) => (
            <GlassCard
              key={testimonial.name}
              className="p-6 transition-all duration-300 hover:scale-105 animate-fade-in"
              style={{ animationDelay: `${index * 100}ms` }}
            >
              <div className="flex flex-col h-full">
                <div className="flex mb-4">
                  {Array.from({ length: testimonial.rating }).map((_, i) => (
                    <Star
                      key={i}
                      className="h-5 w-5 text-yellow-400 fill-yellow-400"
                    />
                  ))}
                </div>
                <p className="text-muted-foreground mb-4 flex-grow">
                  "{testimonial.content}"
                </p>
                <div>
                  <p className="font-semibold">{testimonial.name}</p>
                  <p className="text-sm text-muted-foreground">
                    {testimonial.role}
                  </p>
                </div>
              </div>
            </GlassCard>
          ))}
        </div>
      </div>
    </section>
  );
}
