"use client";

import { GlassCard } from "@/components/ui/glass-card";
import { GradientButton } from "@/components/ui/gradient-button";
import { Upload } from "lucide-react";
import Link from "next/link";

export function CallToAction() {
  return (
    <section className="py-20 px-4">
      <div className="max-w-4xl mx-auto">
        <GlassCard className="p-8 md:p-12 text-center animate-fade-in">
          <h2 className="text-3xl md:text-4xl font-bold mb-6">
            <span className="gradient-text">
              Ready to Transform Your Images?
            </span>
          </h2>
          <p className="text-xl text-muted-foreground mb-8 max-w-2xl mx-auto">
            Experience the power of professional color grading in your browser.
            No installation required.
          </p>
          <div className="flex flex-wrap justify-center gap-4">
            <Link href="/workspace">
              <GradientButton size="lg" className="flex items-center gap-2">
                <Upload className="w-5 h-5" />
                <span>Start Color Grading</span>
              </GradientButton>
            </Link>
            <Link href="/about">
              <GradientButton
                variant="outline"
                size="lg"
                gradientFrom="from-gradients-purple"
                gradientTo="to-gradients-blue"
                className="bg-white"
              >
                Learn More
              </GradientButton>
            </Link>
          </div>
        </GlassCard>
      </div>
    </section>
  );
}
